import axios from 'axios';
import {authHeader, authFileHeader} from 'config/redux/helpers';

export const dataService = {
    updateData,
    updateStatistic,
    updateInPatientHistory,
    setLastUpdate,
    getInPatientHistory,
    getMessHistory,
    getDepartmentHistory,
    updateDepartmentHistory,
    updateMessHistory
};

function getMessHistory() {
    return axios.get(`${process.env.REACT_APP_SERVICE_URL}/api/v2/quarantine/history`, {headers: authHeader()})
        .then(result => {
            return result.data
        });
}

function getInPatientHistory() {
    return axios.get(`${process.env.REACT_APP_SERVICE_URL}/api/v2/inpatient/history`, {headers: authHeader()})
        .then(result => {
            return result.data
        });
}

function getDepartmentHistory() {
    return axios.get(`${process.env.REACT_APP_SERVICE_URL}/api/v2/department/history`, {headers: authHeader()})
        .then(result => {
            return result.data
        });
}

function updateData(file, onUploadProgress) {
    let formData = new FormData();

    formData.append("file", file);

    return axios.post(`${process.env.REACT_APP_SERVICE_URL}/api/statistic/batch`, formData,
        {
            headers: authFileHeader(),
            onUploadProgress
        }
    );
}

function updateStatistic(data) {
    return axios.put(`${process.env.REACT_APP_SERVICE_URL}/api/v2/statistic`, data,
        {
            headers: authHeader(),
        }
    );
}

function updateInPatientHistory(data) {
    return axios.post(`${process.env.REACT_APP_SERVICE_URL}/api/v2/inpatient`, data,
        {
            headers: authHeader(),
        }
    );
}

function updateMessHistory(data) {
    return axios.post(`${process.env.REACT_APP_SERVICE_URL}/api/v2/quarantine/place`, data,
        {
            headers: authHeader(),
        }
    );
}

function updateDepartmentHistory(data) {
    return axios.post(`${process.env.REACT_APP_SERVICE_URL}/api/v2/department`, data,
        {
            headers: authHeader(),
        }
    );
}

function setLastUpdate(date) {
    const data = {
        key: "L01",
        value: date,
    };
    return axios.put(`${process.env.REACT_APP_SERVICE_URL}/api/v2/setting`, data, {headers: authHeader()})
        .then(result => {
            return result.data
        });
}



