import { authConstant } from "../constants";
import { authService } from "config/services";

export const authAction = {
    login,
    logout
}

function login(username, password) {
    return dispatch => {
        dispatch(request({ username }));

        authService.login(username, password)
            .then(
                user => {
                    dispatch(success(user));
                    window.location = "/cms";
                },
                error => {
                    dispatch(failure(error.toString()));
                }
            );
    };

    function request(user) { return { type: authConstant.AUTH_REQUEST, user } }
    function success(user) { return { type: authConstant.AUTH_SUCCESS, user } }
    function failure(error) { return { type: authConstant.AUTH_FAILURE, error } }

}

function logout() {
    return dispatch => {
        dispatch(request());

        authService.logout();
        window.location = "/login";
    }

    function request() { return { type: authConstant.AUTH_LOGOUT } }
}