import React from 'react'
import { Grid, Typography, makeStyles, Paper, Tabs, Tab, Box } from '@material-ui/core'
import CardDashboard from 'components/moleculas/CardDashboard'
import { useDispatch, useSelector } from 'react-redux';
import { useEffect } from 'react';
import { dashboardAction } from 'config/redux/actions';
import CardDashboardRdt from "../../moleculas/CardDashboardRdt";

const useStyles = makeStyles((theme) => ({
    root: {
        padding: theme.spacing(2, 0, 2, 1)
    },
    content: {
        padding: theme.spacing(1)
    },
    title: {
        fontWeight: theme.typography.fontWeightBold
    },
    sectiontitle: {
        borderBottom: '1px solid black'
    }
}))

function SectionStatistic() {
    const statistic = useSelector(state => state.dashboard?.statistic);
    const date = useSelector(state => state.dashboard?.date);
    const dispatch = useDispatch();
    const classes = useStyles();
    useEffect(() => {
        dispatch(dashboardAction.getStatistic());
        dispatch(dashboardAction.getLastUpdate());
    }, [])
    return (
        <Paper elevation={0}>
            <Box className={classes.root}>
                <Typography className={classes.title}>Angka Kejadian Di Kideco</Typography>
                <Typography className={classes.subtitle} color="textSecondary" gutterBottom>Update Terakhir: {date}</Typography>
                <Tabs
                    value={0}
                    indicatorColor="primary"
                    textColor="primary"
                    aria-label="tabs"
                >
                    <Tab label="Statistic" />
                </Tabs>
            </Box>
            <Grid container spacing={1} className={classes.content}>
                <Grid item xs={12} md={6} lg={3}>
                    <CardDashboard 
                        heading="TOTAL TERKONFIRMASI"
                        color="black"
                        left={statistic ? statistic.confirmationKidecoTotal : 0}
                        leftGrow={statistic ? statistic.confirmationKidecoTotalPertumbuhan : 0}
                        right={statistic ? statistic.confirmationPaserTotal : 0}
                        rightGrow={statistic ? statistic.confirmationPaserTotalPertumbuhan : 0} />
                </Grid>
                <Grid item xs={12} md={6} lg={3}>
                    <CardDashboard 
                        heading="ISOLASI/DALAM PERAWATAN"
                        color="#ceb546"
                        left={statistic ? statistic.confirmationKidecoDiisolasi : 0}
                        leftGrow={statistic ? statistic.confirmationKidecoDiisolasiPertumbuhan : 0}
                        right={statistic ? statistic.confirmationPaserDiisolasi : 0}
                        rightGrow={statistic ? statistic.confirmationPaserDiisolasiPertumbuhan : 0} />
                </Grid>
                <Grid item xs={12} md={6} lg={3}>
                    <CardDashboard 
                        heading="SELESAI ISOLASI/SEMBUH"
                        color="#27ae60"
                        left={statistic ? statistic.confirmationKidecoSelesai : 0}
                        leftGrow={statistic ? statistic.confirmationKidecoSelesaiPertumbuhan : 0}
                        right={statistic ? statistic.confirmationPaserSelesai : 0}
                        rightGrow={statistic ? statistic.confirmationPaserSelesaiPertumbuhan : 0} />
                </Grid>
                <Grid item xs={12} md={6} lg={3}>
                    <CardDashboard 
                        heading="MENINGGAL"
                        color="#a20e0e"
                        left={statistic ? statistic.confirmationKidecoMeninggal : 0}
                        leftGrow={statistic ? statistic.confirmationKidecoMeninggalPertumbuhan : 0}
                        right={statistic ? statistic.confirmationPaserMeninggal : 0}
                        rightGrow={statistic ? statistic.confirmationPaserMeninggalPertumbuhan : 0} />
                </Grid>
                <Grid item xs={12}>
                    <CardDashboardRdt
                        heading="MENINGGAL"
                        color="#a20e0e"
                        left={statistic ? statistic.rdtTotal : 0}
                        right={statistic ? statistic.rdtNonReaktif : 0}
                        middle={statistic ? statistic.rdtReaktif : 0}
                        />
                </Grid>
            </Grid>
        </Paper>
    )
}

export default SectionStatistic
