import { dashboardConstant } from "../constants";
import { formatDateDayIndonesia } from "utils/date";

const initState = {
    loading: false,
    images: []
}

export function dashboard(state = initState, action) {
    switch (action.type) {
        case dashboardConstant.DASHBOARD_REQUEST:
            return {
                ...state,
                loading: true
            }
        case dashboardConstant.DASHBOARD_FAILURE:
            return {
                ...state,
                loading: false,
                error: true,
                message: action.error
            }
        case dashboardConstant.DASHBOARD_IMAGES_SUCCESS:
            if (action.result !== null) {
                var images = action.result.map(img => {
                    return {
                        name: img.key,
                        image: img.value
                    }
                })
            } else {
                images = null;
            }
            return {
                ...state,
                loading: false,
                error: false,
                images: images
            }
        case dashboardConstant.DASHBOARD_SUCCESS: 
            var data = action.result;
            if(data.key === "LAST_UPDATED") {
                return {
                    ...state,
                    loading: false,
                    error: false,
                    date: formatDateDayIndonesia(data.value, "DD/MM/YYYY")
                }
            } else {
                return {
                    ...state,
                    loading: false,
                    error: false,
                    results: action.result
                }
            }
        case dashboardConstant.GET_STATISTICS: 
            return {
                ...state,
                loading: false,
                error: false,
                statistic: action.result
            }
        default:
            return state;
    }
}