import React from 'react';
import {Grid, makeStyles, Paper} from "@material-ui/core";
import MyChart from "../../moleculas/MyChart";
import {randomNumber} from "../../../utils/number";

const useStyles = makeStyles((theme) => ({
    root: {
        margin: theme.spacing(2, 0, 5, 0)
    },
    paper: {
        padding: theme.spacing(1)
    }
}))

function SectionRapidTestDiagnostic() {
    const classes = useStyles();
    return (
        <Grid item xs={12} className={classes.root}>
            <Paper elevation={2} className={classes.paper}><MyChart
                    style={{height: 450}}
                    options={{
                        title: {
                            text: "PCR | ANTIBODY | ANTIGEN"
                        },
                        legend: {
                            orient: 'vertical',
                            left: 'right',
                            data: ['Antigen Reactive', 'Antigen Non Reactive', 'Antibody Reactive', 'Antibody Non Reactive', 'Pcr Reactive', 'Pcr Non Reactive']
                        },
                        grid: {
                            top: '10%',
                            left: '3%',
                            right: '2%',
                            bottom: '5%',
                            containLabel: true
                        },
                        color: ["#4B93FF", "#BBD6FF", "#666666", "#D5D5D5", "#FF5A5A", "#FFBEBE"],
                        xAxis: [
                            {
                                type: 'category',
                                data: ['周一', '周二', '周三', '周四', '周五', '周六', '周日']
                            }
                        ],
                        yAxis: [
                            {
                                type: 'value'
                            }
                        ],
                        series: [
                            {
                                name: 'Antigen Reactive',
                                type: 'bar',
                                stack: 'antigen',
                                emphasis: {
                                    focus: 'series'
                                },
                                data: [randomNumber(1, 200), randomNumber(1, 200), randomNumber(1, 200), randomNumber(1, 200), randomNumber(1, 200), randomNumber(1, 200), randomNumber(1, 200)]
                            },
                            {
                                name: 'Antigen Non Reactive',
                                type: 'bar',
                                stack: 'antigen',
                                emphasis: {
                                    focus: 'series'
                                },
                                data: [randomNumber(1, 200), randomNumber(1, 200), randomNumber(1, 200), randomNumber(1, 200), randomNumber(1, 200), randomNumber(1, 200), randomNumber(1, 200)]
                            },
                            {
                                name: 'Antibody Reactive',
                                type: 'bar',
                                stack: 'antibody',
                                emphasis: {
                                    focus: 'series'
                                },
                                data: [randomNumber(1, 200), randomNumber(1, 200), randomNumber(1, 200), randomNumber(1, 200), randomNumber(1, 200), randomNumber(1, 200), randomNumber(1, 200)]
                            },
                            {
                                name: 'Antibody Non Reactive',
                                type: 'bar',
                                stack: 'antibody',
                                emphasis: {
                                    focus: 'series'
                                },
                                data: [randomNumber(1, 200), randomNumber(1, 200), randomNumber(1, 200), randomNumber(1, 200), randomNumber(1, 200), randomNumber(1, 200), randomNumber(1, 200)]
                            },
                            {
                                name: 'Pcr Reactive',
                                type: 'bar',
                                stack: 'pcr',
                                emphasis: {
                                    focus: 'series'
                                },
                                data: [randomNumber(1, 200), randomNumber(1, 200), randomNumber(1, 200), randomNumber(1, 200), randomNumber(1, 200), randomNumber(1, 200), randomNumber(1, 200)]
                            },
                            {
                                name: 'Pcr Non Reactive',
                                type: 'bar',
                                stack: 'pcr',
                                emphasis: {
                                    focus: 'series'
                                },
                                data: [randomNumber(1, 200), randomNumber(1, 200), randomNumber(1, 200), randomNumber(1, 200), randomNumber(1, 200), randomNumber(1, 200), randomNumber(1, 200)]
                            },
                        ]
                    }}
                />
            </Paper>
        </Grid>
    )
}

export default SectionRapidTestDiagnostic;