import {FormControl, Grid, makeStyles, NativeSelect, Paper, Typography} from "@material-ui/core";
import MyChart from "../../../moleculas/MyChart";
import {randomNumber} from "../../../../utils/number";

const useStyles = makeStyles((theme) => ({
    root: {
        padding: theme.spacing(1, 0, 0, 0),
        color: "#707070"
    },
    title: {
        padding: theme.spacing(1, 0, 0, 1),
        fontWeight: theme.typography.fontWeightBold
    },
    paper: {
        borderRadius: theme.spacing(2)
    },
    body: {
        padding: theme.spacing(0),
        color: "#707070"
    },
    subtitle: {
        fontWeight: theme.typography.fontWeightBold
    },
    formControl: {
        margin: theme.spacing(1),
        minWidth: 120,
    },
    selectEmpty: {},
}))

function ChartStatistic() {
    const classes = useStyles();
    return (
        <Grid item xs={12} className={classes.root}>
            <Paper elevation={2} className={classes.paper}>
                <Grid container justify="space-between">
                    <Grid item>
                        <Typography variant={"body2"} className={classes.title} gutterBottom>
                            {`positive monthly`.toUpperCase()}
                        </Typography>
                    </Grid>
                    <Grid item>
                        <FormControl className={classes.formControl}>
                            <NativeSelect
                                className={classes.selectEmpty}
                                defaultValue="total"
                                inputProps={{
                                    'aria-label': 'type'
                                }}
                            >
                                <option value="case">Case</option>
                                <option value="total">Total</option>
                            </NativeSelect>
                        </FormControl>
                    </Grid>

                </Grid>

                <Grid container className={classes.body}>
                    <Grid item xs={12}>
                        <MyChart options={{
                            color: ['#FF5A5A', '#666666', '#A7A7A7'],
                            legend: {
                                data: ['Positive', 'Sembuh']
                            },
                            yAxis: {
                                type: 'value'
                            },
                            xAxis: {
                                type: 'category',
                                data: ['Januari', 'Februari', 'Maret', 'April', 'Juni', 'July', 'Agustus']
                            },
                            series: [
                                {
                                    name: 'Positive',
                                    type: 'bar',
                                    data: [randomNumber(1, 400), randomNumber(1, 400), randomNumber(1, 400), randomNumber(1, 400), randomNumber(1, 400), randomNumber(1, 400), randomNumber(1, 400)]
                                },
                                {
                                    name: 'Sembuh',
                                    type: 'bar',
                                    stack: 'total',
                                    data: [randomNumber(1, 400), randomNumber(1, 400), randomNumber(1, 400), randomNumber(1, 400), randomNumber(1, 400), randomNumber(1, 400), randomNumber(1, 400)]
                                },
                            ]
                        }}/>
                    </Grid>
                </Grid>
            </Paper>
        </Grid>
    )
}

export default ChartStatistic;