import React, {useState} from 'react'
import {
    Grid,
    Typography,
    makeStyles,
    Paper,
    Box,
    Table,
    TableHead,
    TableRow, TableBody, TableContainer, TextField, Button
} from '@material-ui/core'
import {useDispatch, useSelector} from 'react-redux';
import {useEffect} from 'react';
import {dashboardAction, dataAction} from 'config/redux/actions';
import StyledTableCell from "../../moleculas/StyledTableCell";
import DatePicker from "react-date-picker";
import {convertDate} from "../../../utils/date";

const useStyles = makeStyles((theme) => ({
    root: {
        padding: theme.spacing(1, 0, 0, 1)
    },
    content: {
        padding: theme.spacing(1)
    },
    title: {
        fontWeight: theme.typography.fontWeightBold
    },
    subtitle: {},
    button: {
        margin: theme.spacing(2)
    },
    calendar: {
        padding: theme.spacing(1)
    }
}))

function StatisticUpdate() {
    const statistic = useSelector(state => state.dashboard?.statistic);
    const date = useSelector(state => state.dashboard?.date);
    const [value, onChange] = useState(new Date());
    const [dataStatistic, setDataStatistic] = useState({
        confirmationKidecoDiisolasi: 0,
        confirmationKidecoDiisolasiPertumbuhan: 0,
        confirmationKidecoMeninggal: 0,
        confirmationKidecoMeninggalPertumbuhan: 0,
        confirmationKidecoSelesai: 0,
        confirmationKidecoSelesaiPertumbuhan: 0,
        confirmationKidecoTotal: 0,
        confirmationKidecoTotalPertumbuhan: 0,
        confirmationPaserDiisolasi: 0,
        confirmationPaserDiisolasiPertumbuhan: 0,
        confirmationPaserMeninggal: 0,
        confirmationPaserMeninggalPertumbuhan: 0,
        confirmationPaserSelesai: 0,
        confirmationPaserSelesaiPertumbuhan: 0,
        confirmationPaserTotal: 0,
        confirmationPaserTotalPertumbuhan: 0,
        rdtTotal: 0,
        rdtReaktif: 0,
        rdtNonReaktif: 0
    })
    const dispatch = useDispatch();
    const classes = useStyles();

    useEffect(() => {
        dispatch(dashboardAction.getStatistic());
        dispatch(dashboardAction.getLastUpdate());
    }, [])

    useEffect(() => {
        if (statistic) {
            setDataStatistic(statistic);
        }
    }, [statistic])

    const update = () => {
        dispatch(dataAction.updateStatistic({...dataStatistic, date: convertDate(value)}));
    }

    const handleChangeData = (e) => {
        setDataStatistic({...dataStatistic, [e.target.name]: e.target.value});
    }

    return (
        <Paper elevation={1}>
            <Box className={classes.root}>
                <Typography className={classes.title}>Update Data Statistic</Typography>
                <Typography className={classes.subtitle} color="textSecondary">Update Terakhir: {date}</Typography>
            </Box>
            <Grid container spacing={1} className={classes.content}>
                <Grid item xs={12}>
                    <TableContainer>
                        <Table className={classes.table} aria-label="customized table">
                            <TableHead>
                                <TableRow>
                                    <StyledTableCell align="left" rowSpan={2}>Description</StyledTableCell>
                                    <StyledTableCell align="center" colSpan={2}>KIDECO</StyledTableCell>
                                    <StyledTableCell align="center" colSpan={2}>PASER</StyledTableCell>
                                </TableRow>
                                <TableRow>
                                    <StyledTableCell align="left">Last Updated Data</StyledTableCell>
                                    <StyledTableCell align="center" style={{minWidth: 100}}/>
                                    <StyledTableCell align="left">Last Updated Data</StyledTableCell>
                                    <StyledTableCell align="center" style={{minWidth: 100}}/>
                                </TableRow>
                            </TableHead>
                            <TableBody>
                                <TableRow key={1}>
                                    <StyledTableCell component="th" scope="row">
                                        Total Terkonfirmasi
                                    </StyledTableCell>
                                    <StyledTableCell component="th" scope="row">
                                        {statistic ? statistic.confirmationKidecoTotal : 0}
                                    </StyledTableCell>
                                    <StyledTableCell component="th" align="center" scope="row">
                                        <TextField
                                            type="number"
                                            value={dataStatistic.confirmationKidecoTotal}
                                            name="confirmationKidecoTotal"
                                            onChange={handleChangeData}
                                            InputLabelProps={{
                                                shrink: true,
                                            }}
                                        />
                                    </StyledTableCell>
                                    <StyledTableCell component="th" scope="row">
                                        {statistic ? statistic.confirmationPaserTotal : 0}
                                    </StyledTableCell>
                                    <StyledTableCell component="th" align="center" scope="row">
                                        <TextField
                                            type="number"
                                            name="confirmationPaserTotal"
                                            onChange={handleChangeData}
                                            value={dataStatistic.confirmationPaserTotal}
                                            InputLabelProps={{
                                                shrink: true,
                                            }}
                                        />
                                    </StyledTableCell>
                                </TableRow>
                                <TableRow key={2}>
                                    <StyledTableCell component="th" scope="row">
                                        Total Terkonfirmasi Growth
                                    </StyledTableCell>
                                    <StyledTableCell component="th" scope="row">
                                        {statistic ? statistic.confirmationKidecoTotalPertumbuhan : 0}
                                    </StyledTableCell>
                                    <StyledTableCell component="th" align="center" scope="row">
                                        <TextField
                                            type="number"
                                            name="confirmationKidecoTotalPertumbuhan"
                                            onChange={handleChangeData}
                                            value={dataStatistic.confirmationKidecoTotalPertumbuhan}
                                            InputLabelProps={{
                                                shrink: true,
                                            }}
                                        />
                                    </StyledTableCell>
                                    <StyledTableCell component="th" scope="row">
                                        {statistic ? statistic.confirmationPaserTotalPertumbuhan : 0}
                                    </StyledTableCell>
                                    <StyledTableCell component="th" align="center" scope="row">
                                        <TextField
                                            type="number"
                                            name="confirmationPaserTotalPertumbuhan"
                                            onChange={handleChangeData}
                                            value={dataStatistic.confirmationPaserTotalPertumbuhan}
                                            InputLabelProps={{
                                                shrink: true,
                                            }}
                                        />
                                    </StyledTableCell>
                                </TableRow>
                                <TableRow key={3}>
                                    <StyledTableCell component="th" scope="row">
                                        ISOLASI/DALAM PERAWATAN
                                    </StyledTableCell>
                                    <StyledTableCell component="th" scope="row">
                                        {statistic ? statistic.confirmationKidecoDiisolasi : 0}
                                    </StyledTableCell>
                                    <StyledTableCell component="th" align="center" scope="row">
                                        <TextField
                                            type="number"
                                            name="confirmationKidecoDiisolasi"
                                            onChange={handleChangeData}
                                            value={dataStatistic.confirmationKidecoDiisolasi}
                                            InputLabelProps={{
                                                shrink: true,
                                            }}
                                        />
                                    </StyledTableCell>
                                    <StyledTableCell component="th" scope="row">
                                        {statistic ? statistic.confirmationPaserDiisolasi : 0}
                                    </StyledTableCell>
                                    <StyledTableCell component="th" align="center" scope="row">
                                        <TextField
                                            type="number"
                                            name="confirmationPaserDiisolasi"
                                            onChange={handleChangeData}
                                            value={dataStatistic.confirmationPaserDiisolasi}
                                            InputLabelProps={{
                                                shrink: true,
                                            }}
                                        />
                                    </StyledTableCell>
                                </TableRow>
                                <TableRow key={4}>
                                    <StyledTableCell component="th" scope="row">
                                        ISOLASI/DALAM PERAWATAN GROWTH
                                    </StyledTableCell>
                                    <StyledTableCell component="th" scope="row">
                                        {statistic ? statistic.confirmationKidecoDiisolasiPertumbuhan : 0}
                                    </StyledTableCell>
                                    <StyledTableCell component="th" align="center" scope="row">
                                        <TextField
                                            type="number"
                                            name="confirmationKidecoDiisolasiPertumbuhan"
                                            onChange={handleChangeData}
                                            value={dataStatistic.confirmationKidecoDiisolasiPertumbuhan}
                                            InputLabelProps={{
                                                shrink: true,
                                            }}
                                        />
                                    </StyledTableCell>
                                    <StyledTableCell component="th" scope="row">
                                        {statistic ? statistic.confirmationPaserDiisolasiPertumbuhan : 0}
                                    </StyledTableCell>
                                    <StyledTableCell component="th" align="center" scope="row">
                                        <TextField
                                            type="number"
                                            name="confirmationPaserDiisolasiPertumbuhan"
                                            onChange={handleChangeData}
                                            value={dataStatistic.confirmationPaserDiisolasiPertumbuhan}
                                            InputLabelProps={{
                                                shrink: true,
                                            }}
                                        />
                                    </StyledTableCell>
                                </TableRow>
                                <TableRow key={5}>
                                    <StyledTableCell component="th" scope="row">
                                        SELESAI ISOLASI/SEMBUH
                                    </StyledTableCell>
                                    <StyledTableCell component="th" scope="row">
                                        {statistic ? statistic.confirmationKidecoSelesai : 0}
                                    </StyledTableCell>
                                    <StyledTableCell component="th" align="center" scope="row">
                                        <TextField
                                            type="number"
                                            name="confirmationKidecoSelesai"
                                            onChange={handleChangeData}
                                            value={dataStatistic.confirmationKidecoSelesai}
                                            InputLabelProps={{
                                                shrink: true,
                                            }}
                                        />
                                    </StyledTableCell>
                                    <StyledTableCell component="th" scope="row">
                                        {statistic ? statistic.confirmationPaserSelesai : 0}
                                    </StyledTableCell>
                                    <StyledTableCell component="th" align="center" scope="row">
                                        <TextField
                                            type="number"
                                            name="confirmationPaserSelesai"
                                            onChange={handleChangeData}
                                            value={dataStatistic.confirmationPaserSelesai}
                                            InputLabelProps={{
                                                shrink: true,
                                            }}
                                        />
                                    </StyledTableCell>
                                </TableRow>
                                <TableRow key={6}>
                                    <StyledTableCell component="th" scope="row">
                                        SELESAI ISOLASI/SEMBUH GROWTH
                                    </StyledTableCell>
                                    <StyledTableCell component="th" scope="row">
                                        {statistic ? statistic.confirmationKidecoSelesaiPertumbuhan : 0}
                                    </StyledTableCell>
                                    <StyledTableCell component="th" align="center" scope="row">
                                        <TextField
                                            type="number"
                                            name="confirmationKidecoSelesaiPertumbuhan"
                                            onChange={handleChangeData}
                                            value={dataStatistic.confirmationKidecoSelesaiPertumbuhan}
                                            InputLabelProps={{
                                                shrink: true,
                                            }}
                                        />
                                    </StyledTableCell>
                                    <StyledTableCell component="th" scope="row">
                                        {statistic ? statistic.confirmationPaserSelesaiPertumbuhan : 0}
                                    </StyledTableCell>
                                    <StyledTableCell component="th" align="center" scope="row">
                                        <TextField
                                            type="number"
                                            name="confirmationPaserSelesaiPertumbuhan"
                                            onChange={handleChangeData}
                                            value={dataStatistic.confirmationPaserSelesaiPertumbuhan}
                                            InputLabelProps={{
                                                shrink: true,
                                            }}
                                        />
                                    </StyledTableCell>
                                </TableRow>
                                <TableRow key={7}>
                                    <StyledTableCell component="th" scope="row">
                                        MENINGGAL
                                    </StyledTableCell>
                                    <StyledTableCell component="th" scope="row">
                                        {statistic ? statistic.confirmationKidecoMeninggal : 0}
                                    </StyledTableCell>
                                    <StyledTableCell component="th" align="center" scope="row">
                                        <TextField
                                            type="number"
                                            name="confirmationKidecoMeninggal"
                                            onChange={handleChangeData}
                                            value={dataStatistic.confirmationKidecoMeninggal}
                                            InputLabelProps={{
                                                shrink: true,
                                            }}
                                        />
                                    </StyledTableCell>
                                    <StyledTableCell component="th" scope="row">
                                        {statistic ? statistic.confirmationPaserMeninggal : 0}
                                    </StyledTableCell>
                                    <StyledTableCell component="th" align="center" scope="row">
                                        <TextField
                                            type="number"
                                            name="confirmationPaserMeninggal"
                                            onChange={handleChangeData}
                                            value={dataStatistic.confirmationPaserMeninggal}
                                            InputLabelProps={{
                                                shrink: true,
                                            }}
                                        />
                                    </StyledTableCell>
                                </TableRow>
                                <TableRow key={8}>
                                    <StyledTableCell component="th" scope="row">
                                        MENINGGAL GROWTH
                                    </StyledTableCell>
                                    <StyledTableCell component="th" scope="row">
                                        {statistic ? statistic.confirmationKidecoMeninggalPertumbuhan : 0}
                                    </StyledTableCell>
                                    <StyledTableCell component="th" align="center" scope="row">
                                        <TextField
                                            type="number"
                                            name="confirmationKidecoMeninggalPertumbuhan"
                                            onChange={handleChangeData}
                                            value={dataStatistic.confirmationKidecoMeninggalPertumbuhan}
                                            InputLabelProps={{
                                                shrink: true,
                                            }}
                                        />
                                    </StyledTableCell>
                                    <StyledTableCell component="th" scope="row">
                                        {statistic ? statistic.confirmationPaserMeninggalPertumbuhan : 0}
                                    </StyledTableCell>
                                    <StyledTableCell component="th" align="center" scope="row">
                                        <TextField
                                            type="number"
                                            name="confirmationPaserMeninggalPertumbuhan"
                                            onChange={handleChangeData}
                                            value={dataStatistic.confirmationPaserMeninggalPertumbuhan}
                                            InputLabelProps={{
                                                shrink: true,
                                            }}
                                        />
                                    </StyledTableCell>
                                </TableRow>
                            </TableBody>
                        </Table>
                    </TableContainer>
                </Grid>
            </Grid>
            <Box className={classes.root}>
                <Typography className={classes.title}>Data RDT</Typography>
            </Box>
            <Grid container spacing={1} className={classes.content}>
                <Grid item xs={12}>
                    <TableContainer>
                        <Table className={classes.table} aria-label="customized table">
                            <TableHead>
                                <TableRow>
                                    <StyledTableCell align="left">Description</StyledTableCell>
                                    <StyledTableCell align="left">Last Updated Data</StyledTableCell>
                                    <StyledTableCell align="center" style={{minWidth: 100}}/>
                                </TableRow>
                            </TableHead>
                            <TableBody>
                                <TableRow key={1}>
                                    <StyledTableCell component="th" scope="row">
                                        Reaktif
                                    </StyledTableCell>
                                    <StyledTableCell component="th" scope="row">
                                        {statistic ? statistic.rdtReaktif : 0}
                                    </StyledTableCell>
                                    <StyledTableCell component="th" align="center" scope="row">
                                        <TextField
                                            type="number"
                                            name="rdtReaktif"
                                            onChange={handleChangeData}
                                            value={dataStatistic.rdtReaktif}
                                            InputLabelProps={{
                                                shrink: true,
                                            }}
                                        />
                                    </StyledTableCell>
                                </TableRow>
                                <TableRow key={1}>
                                    <StyledTableCell component="th" scope="row">
                                        Non Reaktif
                                    </StyledTableCell>
                                    <StyledTableCell component="th" scope="row">
                                        {statistic ? statistic.rdtNonReaktif : 0}
                                    </StyledTableCell>
                                    <StyledTableCell component="th" align="center" scope="row">
                                        <TextField
                                            type="number"
                                            name="rdtNonReaktif"
                                            onChange={handleChangeData}
                                            value={dataStatistic.rdtNonReaktif}
                                            InputLabelProps={{
                                                shrink: true,
                                            }}
                                        />
                                    </StyledTableCell>
                                </TableRow>
                                <TableRow key={1}>
                                    <StyledTableCell component="th" scope="row">
                                        Total
                                    </StyledTableCell>
                                    <StyledTableCell component="th" scope="row">
                                        {statistic ? statistic.rdtTotal : 0}
                                    </StyledTableCell>
                                    <StyledTableCell component="th" align="center" scope="row"/>
                                </TableRow>
                            </TableBody>
                        </Table>
                    </TableContainer>
                </Grid>
            </Grid>
            <Grid container spacing={1} className={classes.content}>
                <Grid item xs={12}>
                    <Typography className={classes.subtitle} color="textSecondary">Date</Typography>
                    <DatePicker
                        onChange={onChange}
                        value={value}
                        className={classes.calendar}
                        format="dd/MM/y"
                    />
                </Grid>
            </Grid>


            <Button
                variant="contained"
                color="secondary"
                className={classes.button}
                onClick={() => update()}
            >
                Update
            </Button>
        </Paper>
    )
}

export default StatisticUpdate;
