import React from 'react'
import {
    makeStyles,
    Paper, Grid
} from '@material-ui/core';
import GroupChart from "../SectionStatisticKideco/GroupChart";
import GroupPieChart from "../../moleculas/GroupPieChart";

const useStyles = makeStyles((theme) => ({
    root: {
        padding: theme.spacing(4, 0, 0, 0)
    },
    header: {
        padding: theme.spacing(1)
    },
    content: {
        padding: theme.spacing(1)
    },
    title: {
        fontWeight: theme.typography.fontWeightBold
    },
    rootStyle: {
        color: "#707070"
    }
}));

const chart = {
    color: ['#FF5A5A', '#666666', '#A7A7A7'],
    tooltip: {
        trigger: 'axis',
        axisPointer: {            // Use axis to trigger tooltip
            type: 'shadow'        // 'shadow' as default; can also be 'line' or 'shadow'
        }
    },
    grid: {
        left: '3%',
        right: '4%',
        bottom: '3%',
        containLabel: true
    },
    legend: {
        data: ['Kideco', 'Family', 'Mitra + Fam']
    },
    yAxis: {
        type: 'category',
        data: [
            'RS Muhammadiyah',
            'RS Pertamina Balikpapan',
            'RS Balikpapan Baru',
            'Isolasi Petrosea',
            'Mess KM40',
            'Mess 9 BC',
            'Rumah Pribadi'
        ]
    },
    xAxis: {
        type: 'value'
    },
    series: [
        {
            name: 'Kideco',
            type: 'bar',
            stack: 'total',
            label: {
                show: true
            },
            emphasis: {
                focus: 'series'
            },
            data: [320, 302, 301, 334, 390, 330, 320]
        },
        {
            name: 'Family',
            type: 'bar',
            stack: 'total',
            label: {
                show: true
            },
            emphasis: {
                focus: 'series'
            },
            data: [120, 132, 101, 134, 90, 230, 210]
        },
        {
            name: 'Mitra + Fam',
            type: 'bar',
            stack: 'total',
            label: {
                show: true
            },
            emphasis: {
                focus: 'series'
            },
            data: [220, 182, 191, 234, 290, 330, 310]
        },
    ]
}


function SectionQuarantinePlace(props) {
    const {rows} = props;
    const classes = useStyles();
    return (
        <Grid container spacing={1} className={classes.root}>
            <Grid item xs={12}>
                <Paper elevation={2}>
                    <Grid container spacing={1} className={classes.content}>
                        <Grid item xs={12} md={5}>
                            <GroupChart
                                style={{height: 400}}
                                title="Quarantine Place"
                                rootStyle={classes.rootStyle}
                                elevation={0}
                                options={chart}
                            />
                        </Grid>
                        <Grid item xs={12} md={7}>
                            <GroupPieChart title="Availability"/>
                        </Grid>
                    </Grid>
                </Paper>
            </Grid>
        </Grid>

    );
}

export default SectionQuarantinePlace
