import React, {useState} from "react";
import {Box, Button, Grid, makeStyles, Paper, Typography} from "@material-ui/core";
import Statistic from "./Statistic";
import Cause from "./Cause";
import ChartStatistic from "./ChartStatistic";
import GroupChart from "./GroupChart";
import {randomNumber} from "../../../utils/number";

const useStyles = makeStyles((theme) => ({
    root: {
        padding: theme.spacing(4, 0, 0, 0)
    },
    title: {
        fontWeight: theme.typography.fontWeightBold
    },
    button: {
        margin: theme.spacing(0, 1, 1, 0)
    },
    styleChart: {
        minHeight: 700
    }
}))

const chart = {
    color: ['#FF5A5A', '#666666', '#A7A7A7'],
    tooltip: {
        trigger: 'axis',
        axisPointer: {            // Use axis to trigger tooltip
            type: 'shadow'        // 'shadow' as default; can also be 'line' or 'shadow'
        }
    },
    grid: {
        top: 0,
        left: '3%',
        right: '4%',
        bottom: '5%',
        containLabel: true
    },
    legend: {
        left: 'center',
        orient: 'horizontal',
        bottom: 5,
        data: ['Kideco', 'Family', 'Mitra + Fam']
    },
    yAxis: {
        type: 'category',
        data: ['Safety & Environtment', 'Production', 'Development', 'Geology & Survey', 'Geotechnocal Engineering', 'MID', 'External Relation', 'Legal', 'Planning', 'Quality Control', 'Finance & Accpunting', 'ICT', 'Management Support', 'Procurement & HEM', 'Mine Facility', 'Road Maintenance', 'TMCT']
    },
    xAxis: {
        type: 'value'
    },
    series: [
        {
            name: 'Kideco',
            type: 'bar',
            stack: 'total',
            label: {
                show: true
            },
            emphasis: {
                focus: 'series'
            },
            data: [
                randomNumber(1, 200), randomNumber(1, 200), randomNumber(1, 200), randomNumber(1, 200), randomNumber(1, 200), randomNumber(1, 200), randomNumber(1, 200),
                randomNumber(1, 200), randomNumber(1, 200), randomNumber(1, 200), randomNumber(1, 200), randomNumber(1, 200), randomNumber(1, 200), randomNumber(1, 200),
                randomNumber(1, 200), randomNumber(1, 200), randomNumber(1, 200)
            ]
        },
        {
            name: 'Family',
            type: 'bar',
            stack: 'total',
            label: {
                show: true
            },
            emphasis: {
                focus: 'series'
            },
            data: [
                randomNumber(1, 200), randomNumber(1, 200), randomNumber(1, 200), randomNumber(1, 200), randomNumber(1, 200), randomNumber(1, 200), randomNumber(1, 200),
                randomNumber(1, 200), randomNumber(1, 200), randomNumber(1, 200), randomNumber(1, 200), randomNumber(1, 200), randomNumber(1, 200), randomNumber(1, 200),
                randomNumber(1, 200), randomNumber(1, 200), randomNumber(1, 200)
            ]
        },
        {
            name: 'Mitra + Fam',
            type: 'bar',
            stack: 'total',
            label: {
                show: true
            },
            emphasis: {
                focus: 'series'
            },
            data: [
                randomNumber(1, 200), randomNumber(1, 200), randomNumber(1, 200), randomNumber(1, 200), randomNumber(1, 200), randomNumber(1, 200), randomNumber(1, 200),
                randomNumber(1, 200), randomNumber(1, 200), randomNumber(1, 200), randomNumber(1, 200), randomNumber(1, 200), randomNumber(1, 200), randomNumber(1, 200),
                randomNumber(1, 200), randomNumber(1, 200), randomNumber(1, 200)
            ]
        },
    ]
}

function SectionStatisticKideco(props) {
    const {date} = props;
    const classes = useStyles();
    const [actives, setActive] = useState({
        all: true,
        kideco: false,
        kontraktor: false,
    });
    return (
        <Paper elevation={0} className={classes.root}>
            <Grid container>
                <Grid item xs={12}>
                    <Box className={classes.root}>
                        <ButtonCustom title="All" active={actives.all} click={() => setActive({
                            all: true,
                            kideco: false,
                            kontraktor: false,
                        })}/>
                        <ButtonCustom title="Kideco" active={actives.kideco} click={() => setActive({
                            all: false,
                            kideco: true,
                            kontraktor: false,
                        })}/>
                        <ButtonCustom title="Kontraktor" active={actives.kontraktor} click={() => setActive({
                            all: false,
                            kideco: false,
                            kontraktor: true,
                        })}/>
                        <Typography variant="body2" color="textSecondary" gutterBottom>Update
                            Terakhir: {date}</Typography>
                    </Box>
                </Grid>
                <Grid container xs={12} md={12} lg={6}>
                    {/*Tab Statistic */}
                    <Statistic/>
                    {/*Tab Chart */}
                    <ChartStatistic/>
                    {/*Tab Cause */}
                    <Cause/>
                </Grid>
                <Grid container xs={12} md={12} lg={6} spacing={2}>
                    {/*  Group Chart  */}
                    <GroupChart style={{height: 800}} options={chart}/>
                </Grid>
            </Grid>
        </Paper>
    )
}

function ButtonCustom(props) {
    const {title, active, click} = props;
    const classes = useStyles();
    return (
        <Button variant="contained"
                onClick={click}
                style={{backgroundColor: `${active ? "#5aaa4e" : "#fff"}`, color: `${active ? "#fff" : "#5aaa4e"}`}}
                className={classes.button}>
            <span>{title}</span>
        </Button>
    )

}

export default SectionStatisticKideco;