import React, {useEffect, useState} from 'react'
import {
    withStyles,
    makeStyles,
    Table,
    TableContainer,
    TableHead,
    TableRow,
    TableBody,
    TableCell, Paper, Box, Typography, Grid
} from '@material-ui/core';

const useStyles = makeStyles((theme) => ({
    root: {
        margin: theme.spacing(2, 0, 2, 1)
    },
    header: {
        padding: theme.spacing(1)
    },
    content: {
        padding: theme.spacing(1)
    },
    title: {
        fontWeight: theme.typography.fontWeightBold
    },
    table: {
        minWidth: 700,
    },
    yellow: {
        backgroundColor: 'yellow'
    },
    orange: {
        backgroundColor: 'orange'
    }
}));

const StyledTableCell = withStyles((theme) => ({
    head: {
        backgroundColor: theme.palette.common.white,
        color: theme.palette.common.black,
    },
    body: {
        fontSize: 14,
    },
}))(TableCell);

function SectionPatientInCare(props) {
    const {rows} = props;
    const classes = useStyles();
    return (
        <Paper elevation={2} className={classes.root}>
            <Box className={classes.header}>
                <Typography className={classes.title} variant={"h5"}>In-patient care (Pasien dalam
                    Perawatan)</Typography>
            </Box>
            <Grid container spacing={1} className={classes.content}>
                <TableContainer>
                    <Table className={classes.table} aria-label="customized table">
                        <TableHead>
                            <TableRow>
                                <StyledTableCell rowSpan={2}>Description</StyledTableCell>
                                <StyledTableCell align="center" colSpan={2}>KIDECO</StyledTableCell>
                                <StyledTableCell align="center" rowSpan={2}>Contract Service (Mitra)</StyledTableCell>
                                <StyledTableCell rowSpan={2}>Location</StyledTableCell>
                                <StyledTableCell rowSpan={2}>Status</StyledTableCell>
                            </TableRow>
                            <TableRow>
                                <StyledTableCell align="center">Employee</StyledTableCell>
                                <StyledTableCell align="center">Family</StyledTableCell>
                            </TableRow>
                        </TableHead>
                        <TableBody>
                            {rows && rows.map((row, key) => (
                                <TableRow key={key}>
                                    <StyledTableCell component="th" scope="row">
                                        {row.name}
                                    </StyledTableCell>
                                    <StyledTableCell component="th" scope="row" align="center">
                                        {row.kidecoEmployee}
                                    </StyledTableCell>
                                    <StyledTableCell component="th" scope="row" align="center">
                                        {row.kidecoFamily}
                                    </StyledTableCell>
                                    <StyledTableCell component="th" scope="row" align="center">
                                        {row.contractService}
                                    </StyledTableCell>
                                    <StyledTableCell component="th" scope="row">
                                        {row.location}
                                    </StyledTableCell>
                                    <StyledTableCell component="th" scope="row">
                                        {row.status}
                                    </StyledTableCell>
                                </TableRow>
                            ))}
                            <TableRow className={classes.yellow}>
                                <StyledTableCell component="th" scope="row">
                                    Total
                                </StyledTableCell>
                                <StyledTableCell component="th" scope="row" align="center">
                                    {rows && rows.reduce((total, current) => {
                                        total = total + current.kidecoEmployee;

                                        return total
                                    }, 0)}
                                </StyledTableCell>
                                <StyledTableCell component="th" scope="row" align="center">
                                    {rows && rows.reduce((total, current) => {
                                        total = total + current.kidecoFamily;

                                        return total
                                    }, 0)}
                                </StyledTableCell>
                                <StyledTableCell component="th" scope="row" align="center">
                                    {rows && rows.reduce((total, current) => {
                                        total = total + current.contractService;

                                        return total
                                    }, 0)}
                                </StyledTableCell>
                                <StyledTableCell component="th" scope="row">
                                    {rows && rows.reduce((total, current) => {
                                        total = total + current.contractService + current.kidecoEmployee + current.kidecoFamily;

                                        return total
                                    }, 0)}
                                </StyledTableCell>
                                <StyledTableCell component="th" scope="row" />
                            </TableRow>
                        </TableBody>
                    </Table>
                </TableContainer>
            </Grid>
        </Paper>

    );
}

export default SectionPatientInCare
