import {Grid, makeStyles, Paper, Typography} from "@material-ui/core";
import React from "react";
import {randomNumber} from "../../../../utils/number";
import MyChart from "../../../moleculas/MyChart";

const useStyles = makeStyles((theme) => ({
    root: {
        padding: theme.spacing(1, 0, 0, 0),
        color: "#707070"
    },
    title: {
        padding: theme.spacing(1, 0, 0, 1),
        fontWeight: theme.typography.fontWeightBold
    },
    paper: {
        minHeight: 250,
        borderRadius: theme.spacing(2),
        padding: theme.spacing(1)
    },
    body: {
        padding: theme.spacing(0, 0, 1, 5),
        color: "#707070"
    },
    subtitle: {
        fontWeight: theme.typography.fontWeightBold
    },
    table: {
        fontSize: "10px"
    }
}))

function Cause() {
    const classes = useStyles();
    return (
        <Grid container xs={12} spacing={1} className={classes.root}>
            <Grid item xs={12} md={6}>
                <Paper elevation={2} className={classes.paper}>
                    <Typography variant={"body2"} className={classes.title} gutterBottom>
                        {`cause`.toUpperCase()}
                    </Typography>
                    <MyChart
                        style={{height: '300px'}}
                        options={{
                            legend: {
                                data: ['Cuti Luar / Dinas', 'Cuti Lokal', 'Sakit', 'Tracing'],
                                bottom: 'bottom'
                            },
                            grid: {
                                top: '10%',
                                left: '3%',
                                right: '2%',
                                bottom: '5%',
                                containLabel: true
                            },
                            series: [
                                {
                                    name: 'Data Penyebab',
                                    type: 'pie',
                                    radius: '50%',
                                    data: [
                                        {value: randomNumber(1, 200), name: 'Cuti Luar / Dinas'},
                                        {value: randomNumber(1, 200), name: 'Cuti Lokal'},
                                        {value: randomNumber(1, 200), name: 'Sakit'},
                                        {value: randomNumber(1, 200), name: 'Tracing'}
                                    ],
                                    emphasis: {
                                        itemStyle: {
                                            shadowBlur: 10,
                                            shadowOffsetX: 0,
                                            shadowColor: 'rgba(0, 0, 0, 0.5)'
                                        }
                                    }
                                }
                            ]
                        }}
                    />
                </Paper>
            </Grid>
            <Grid item xs={12} md={6}>
                <Paper elevation={2} className={classes.paper}>
                    <Typography variant={"body2"} className={classes.title} gutterBottom>
                        {`history update`.toUpperCase()}
                    </Typography>
                    <table className={classes.table}>
                        <tr>
                            <td>27 Feb 2021</td>
                            <td>Positive 2(K1), Recovery 2 (F2)</td>
                        </tr>
                        <tr>
                            <td>25 Feb 2021</td>
                            <td>Positive 2(K1), Recovery 2 (F2)</td>
                        </tr>
                        <tr>
                            <td>18 Feb 2021</td>
                            <td>Positive 2(K1), Recovery 2 (F2)</td>
                        </tr>
                        <tr>
                            <td>15 Feb 2021</td>
                            <td>Positive 2(K1), Recovery 2 (F2)</td>
                        </tr>
                        <tr>
                            <td>25 Jan 2021</td>
                            <td>Positive 2(K1), Recovery 2 (F2)</td>
                        </tr>
                    </table>
                </Paper>
            </Grid>
        </Grid>
    )
}

export default Cause;