import React, {useEffect} from 'react'
import {makeStyles, Paper, Grid, Typography} from '@material-ui/core'
import ImageSlider from 'components/moleculas/ImageSlider';
import {Call, WhatsApp, Twitter, Instagram} from '@material-ui/icons';
import {useDispatch} from 'react-redux';
import {dashboardAction} from 'config/redux/actions';

const useStyles = makeStyles((theme) => ({
    root: {
        padding: theme.spacing(1, 0, 0, 1)
    },
    title: {
        fontWeight: theme.typography.fontWeightBold
    },
    sectiontitle: {
        borderBottom: '1px solid black'
    },
    callcentre: {
        padding: theme.spacing(3)
    },
    informasi: {
        padding: theme.spacing(5, 0, 5, 3),
        minHeight: 160
    },
    announcement: {
        padding: theme.spacing(3)
    }
}))

function WelcomeSection() {
    const classes = useStyles();
    const dispatch = useDispatch();
    useEffect(() => {
        dispatch(dashboardAction.getImages());
    }, [])
    return (
        <Paper elevation={0} className={classes.root}>
            <Grid container spacing={1}>
                <Grid container xs={12} md={12} lg={6}>
                    <Grid item xs={12}>
                        <ImageSlider/>
                    </Grid>
                </Grid>
                <Grid container xs={12} md={12} lg={6} spacing={2}>
                    <Grid item xs={12} md={6} lg={6}>
                        <Paper className={classes.callcentre} elevation={2}>
                            <Typography className={classes.title}>{`Call Center`.toUpperCase()}</Typography>
                            <Typography className={classes.subtitle} color="textSecondary" gutterBottom>Nomor
                                Darurat Safety</Typography>
                            <div style={{
                                display: 'flex',
                                alignItems: 'center',
                                flexWrap: 'wrap',
                                marginTop: 15,
                                color: 'green'
                            }}>
                                <Call fontSize="large"/>
                                <Typography style={{paddingTop: 7, paddingLeft: 10, fontWeight: 'bold'}} variant="h5"
                                            gutterBottom>0811-5300-111</Typography>
                            </div>
                        </Paper>
                    </Grid>
                    <Grid item xs={12} md={6} lg={6}>
                        <Paper className={classes.callcentre} elevation={2}>
                            <Typography className={classes.title}>{`Hotline`.toUpperCase()}</Typography>
                            <Typography className={classes.subtitle} color="textSecondary" gutterBottom>Chat WA Seputar
                                COVID-19</Typography>
                            <div style={{
                                display: 'flex',
                                alignItems: 'center',
                                flexWrap: 'wrap',
                                marginTop: 15,
                                color: 'green'
                            }}>
                                <WhatsApp fontSize="large"/>
                                <Typography style={{paddingTop: 7, paddingLeft: 10, fontWeight: 'bold'}} variant="h5"
                                            gutterBottom>0811-5300-222</Typography>
                            </div>
                        </Paper>
                    </Grid>
                    <Grid item xs={12}>
                        <Paper className={classes.callcentre} elevation={2}>
                            <Typography className={classes.title}>ANNOUNCEMENT</Typography>
                            <Grid item xs={12}>
                                <Typography variant='h5' className={classes.announcement}>
                                    Perpanjangan Pengaturan Pola Kerja/ WFH sampai dengan {`15 Maret 2021`}
                                </Typography>
                            </Grid>
                        </Paper>
                    </Grid>
                </Grid>
            </Grid>
        </Paper>
    )
}

export default WelcomeSection
