import React, {useState} from 'react'
import {
    Grid,
    Typography,
    makeStyles,
    Paper,
    Box,
    Table,
    TableHead,
    TableRow, TableBody, TableContainer, TextField, Button
} from '@material-ui/core'
import {useDispatch, useSelector} from 'react-redux';
import {useEffect} from 'react';
import {dashboardAction, dataAction} from 'config/redux/actions';
import StyledTableCell from "../../moleculas/StyledTableCell";
import DatePicker from "react-date-picker";
import {convertDate} from "../../../utils/date";

const useStyles = makeStyles((theme) => ({
    root: {
        padding: theme.spacing(1, 0, 0, 1)
    },
    content: {
        padding: theme.spacing(1)
    },
    title: {
        fontWeight: theme.typography.fontWeightBold
    },
    subtitle: {},
    button: {
        margin: theme.spacing(2)
    },
    calendar: {
        padding: theme.spacing(1)
    }
}))

function InPatientCareUpdate() {
    const inpatient = useSelector(state => state.data?.inpatient);
    const date = useSelector(state => state.dashboard?.date);
    const [value, onChange] = useState(new Date());
    const [patients, setPatient] = useState([])
    const dispatch = useDispatch();
    const classes = useStyles();

    useEffect(() => {
        dispatch(dataAction.getInPatientHistory());
        dispatch(dashboardAction.getLastUpdate());
    }, [])

    useEffect(() => {
        if (inpatient) {
            setPatient(inpatient);
        }
    }, [inpatient])

    const update = () => {
        dispatch(dataAction.updateInPatientHistory({
            date: convertDate(value),
            histories: patients
        }));
    }

    const handleChangeData = (name, value, code) => {
        setPatient(patients.map(patient => {
            if(patient.code === code) {
                return {
                    ...patient,
                    [name]: value
                }
            }

            return patient;
        }))
    }

    return (
        <Paper elevation={1}>
            <Box className={classes.root}>
                <Typography className={classes.title}>Update Data Perawatan Covid</Typography>
                <Typography className={classes.subtitle} color="textSecondary">Update Terakhir: {date}</Typography>
            </Box>
            <Grid container spacing={1} className={classes.content}>
                <Grid item xs={12}>
                    <TableContainer>
                        <Table className={classes.table} aria-label="customized table">
                            <TableHead>
                                <TableRow>
                                    <StyledTableCell align="left" rowSpan={2}>Description</StyledTableCell>
                                    <StyledTableCell align="center" colSpan={2}>KIDECO</StyledTableCell>
                                    <StyledTableCell align="center" rowSpan={2} style={{minWidth: 100}}>Contract Service
                                        (Mitra)</StyledTableCell>
                                    <StyledTableCell align="center" rowSpan={2} style={{minWidth: 100}}>Lokasi</StyledTableCell>
                                    <StyledTableCell align="center" rowSpan={2} style={{minWidth: 100}}>Status</StyledTableCell>
                                </TableRow>
                                <TableRow>
                                    <StyledTableCell align="center" style={{minWidth: 100}}>Karyawan</StyledTableCell>
                                    <StyledTableCell align="center" style={{minWidth: 100}}>Keluarga</StyledTableCell>
                                </TableRow>
                            </TableHead>
                            <TableBody>
                                {patients.map((patient, key) => (
                                    <TableRow key={key}>
                                        <StyledTableCell component="th" scope="row" align="left">
                                            {patient.name}
                                        </StyledTableCell>
                                        <StyledTableCell component="th" align="center" scope="row">
                                            <TextField
                                                type="number"
                                                value={patient.kidecoEmployee}
                                                name="kidecoEmployee"
                                                onChange={(e) => {
                                                    handleChangeData(e.target.name, e.target.value, patient.code)
                                                }}
                                                InputLabelProps={{
                                                    shrink: true,
                                                }}
                                            />
                                        </StyledTableCell>
                                        <StyledTableCell component="th" align="center" scope="row">
                                            <TextField
                                                type="number"
                                                name="kidecoFamily"
                                                onChange={(e) => {
                                                    handleChangeData(e.target.name, e.target.value, patient.code)
                                                }}
                                                value={patient.kidecoFamily}
                                                InputLabelProps={{
                                                    shrink: true,
                                                }}
                                            />
                                        </StyledTableCell>
                                        <StyledTableCell component="th" align="center" scope="row">
                                            <TextField
                                                type="number"
                                                value={patient.contractService}
                                                name="contractService"
                                                onChange={(e) => {
                                                    handleChangeData(e.target.name, e.target.value, patient.code)
                                                }}
                                                InputLabelProps={{
                                                    shrink: true,
                                                }}
                                            />
                                        </StyledTableCell>
                                        <StyledTableCell component="th" align="center" scope="row">
                                            <TextField
                                                type="text"
                                                name="location"
                                                onChange={(e) => {
                                                    handleChangeData(e.target.name, e.target.value, patient.code)
                                                }}
                                                value={patient.location}
                                                InputLabelProps={{
                                                    shrink: true,
                                                }}
                                            />
                                        </StyledTableCell>
                                        <StyledTableCell component="th" align="center" scope="row">
                                            <TextField
                                                type="text"
                                                name="status"
                                                onChange={(e) => {
                                                    handleChangeData(e.target.name, e.target.value, patient.code)
                                                }}
                                                value={patient.status}
                                                InputLabelProps={{
                                                    shrink: true,
                                                }}
                                            />
                                        </StyledTableCell>
                                    </TableRow>
                                ))}
                            </TableBody>
                        </Table>
                    </TableContainer>
                </Grid>
            </Grid>
            <Grid container spacing={1} className={classes.content}>
                <Grid item xs={12}>
                    <Typography className={classes.subtitle} color="textSecondary">Date</Typography>
                    <DatePicker
                        onChange={onChange}
                        value={value}
                        className={classes.calendar}
                        format="dd/MM/y"
                    />
                </Grid>
            </Grid>


            <Button
                variant="contained"
                color="secondary"
                className={classes.button}
                onClick={() => update()}
            >
                Update
            </Button>
        </Paper>
    )
}

export default InPatientCareUpdate;
