import React from 'react'
import {Grid, Typography, makeStyles, Paper, Box, CardContent, Card} from '@material-ui/core'
import { useDispatch, useSelector } from 'react-redux';
import { useEffect } from 'react';
import { dashboardAction } from 'config/redux/actions';
import CardDashboardRdt from "../../moleculas/CardDashboardRdt";
import {formatNumber} from "../../../utils/number";

const useStyles = makeStyles((theme) => ({
    root: {
        padding: theme.spacing(1, 0, 0, 1)
    },
    content: {
        padding: theme.spacing(1)
    },
    title: {
        fontWeight: theme.typography.fontWeightBold
    },
    subtitle: {

    }
}))

function FormUpdate() {
    const statistic = useSelector(state => state.dashboard?.statistic);
    const date = useSelector(state => state.dashboard?.date);
    const dispatch = useDispatch();
    const classes = useStyles();
    useEffect(() => {
        dispatch(dashboardAction.getStatistic());
        dispatch(dashboardAction.getLastUpdate());
    }, [])
    return (
        <Paper elevation={1}>
            <Box className={classes.root}>
                <Typography className={classes.title}>Update Data Covid</Typography>
                <Typography className={classes.subtitle} color="textSecondary">Update Terakhir: {date}</Typography>
            </Box>
            <Grid container spacing={1} className={classes.content}>
                <Grid item xs={12}>
                    <Card className={classes.root} elevation={0}>
                        <CardContent>
                            <Typography className={classes.title} color="textSecondary" gutterBottom>
                                RDT (Rapid Diagnostic Test)
                            </Typography>
                            <Typography className={classes.subtitle} color="textSecondary" gutterBottom>
                                Rapid Diagnostic Test (RDT) atau test diagnostik cepat merupakan test yang digunakan sebagai skrining medis awal untuk mendeteksi COVID-19. Pada hasil RDT yang Reaktif akan dilakukan pemeriksaan konfirmasi lebih lanjut dengan metode Polymerase Chain Reaction (PCR).
                            </Typography>
                            <Grid display="flex" container>

                            </Grid>
                        </CardContent>
                    </Card>
                </Grid>
            </Grid>
        </Paper>
    )
}

export default FormUpdate;
