import * as React from "react";

import useStyles from './style';
import { List, ListItem, ListItemText, IconButton, Drawer } from "@material-ui/core";
import { Menu } from "@material-ui/icons";
import { useDispatch } from "react-redux";
import { Link } from "react-router-dom";
import Cookie from "js-cookie"
import {authAction} from "../../../config/redux/actions";

const SideDrawer = ({navLinks}) => {
    const classes = useStyles();
    const dispatch = useDispatch()
    const [state, setState] = React.useState({ right: false });
    
    const toggleDrawer = (anchor, open) => event => {
        if (
            event.type === "keydown" &&
            (event.key === "Tab" || event.key === "Shift")
        ) {
            return;
        }
        setState({ [anchor]: open });
    };

    const handleLogout = () => {
        dispatch(authAction.logout());
    }
    
    const sideDrawerList = anchor => (
        <div
            className={classes.list}
            role="presentation"
            onClick={toggleDrawer(anchor, false)}
            onKeyDown={toggleDrawer(anchor, false)}
        >
            <List component="nav">
                { !Cookie.get('token') && (
                    <Link to="/" className={classes.linkText}>
                        <ListItem button>
                            <ListItemText secondary={`Home`} />
                        </ListItem>
                    </Link>
                )}
                { Cookie.get('token') && (
                    <Link to="/logout" className={classes.linkText} onClick={handleLogout}>
                        <ListItem button>
                            <ListItemText secondary={`Sign Out`} />
                        </ListItem>
                    </Link>
                )}
            </List>
        </div>
    );

    return (
        <React.Fragment>
          <IconButton
            edge="start"
            aria-label="menu"
            onClick={toggleDrawer("right", true)}
          >
            <Menu fontSize="large" style={{ color: `black` }} />
          </IconButton>
    
          <Drawer
            anchor="right"
            open={state.right}
            onOpen={toggleDrawer("right", true)}
            onClose={toggleDrawer("right", false)}
          >
            {sideDrawerList("right")}
          </Drawer>
        </React.Fragment>
      );
}

export default SideDrawer;