import React, {useEffect} from 'react'
import { Grid, makeStyles } from '@material-ui/core'
import SectionStatistic from 'components/organisms/SectionStatistic'
import WelcomeSection from 'components/organisms/WelcomeSection';
import SectionCovidDepartment from "../../../components/organisms/SectionCovidDepartment";
import SectionQuarantinePlace from "../../../components/organisms/SectionQuarantinePlace";
import SectionPatientInCare from "../../../components/organisms/SectionPatientInCare";
import {dataAction} from "../../../config/redux/actions";
import {useDispatch, useSelector} from "react-redux";
import SectionStatisticKideco from "../../../components/organisms/SectionStatisticKideco";
import SectionRapidTestDiagnostic from "../../../components/organisms/SectionRapidTestDiagnostic";

const useStyles = makeStyles((theme) => ({
    root: {
        padding: theme.spacing(1, 0, 0, 0),
        width: "100%"
    }
}))

function Home() {
    const patients = useSelector(state => state.data?.inpatient);
    const mess = useSelector(state => state.data?.mess);
    const places = useSelector(state => state.data?.places);
    const classes = useStyles();
    const dispatch = useDispatch();
    useEffect(() => {
        dispatch(dataAction.getInPatientHistory());
        dispatch(dataAction.getMessHistory());
        dispatch(dataAction.getDepartmentHistory());
    }, [])
    return (
        <Grid container className={classes.root}>
            <WelcomeSection />
            <SectionStatisticKideco />
            {/*<SectionStatistic />*/}
            {/*<SectionPatientInCare rows={patients} />*/}
            <SectionQuarantinePlace />
            <SectionRapidTestDiagnostic />
            {/*<SectionCovidDepartment rows={places} />*/}
        </Grid>
    )
}

export default Home
