import {dataService} from "config/services";
import {dataConstant} from "../constants";

export const dataAction = {
    updateDataCovid,
    setLastUpdate,
    updateStatistic,
    getInPatientHistory,
    updateInPatientHistory,
    getMessHistory,
    getDepartmentHistory,
    updateMessHistory,
    updateDepartmentHistory
}

function updateMessHistory(data) {
    return dispatch => {
        dispatch(request());

        dataService.updateMessHistory(data)
            .then(
                result => dispatch(success(result)),
                error => dispatch(failure(error.toString()))
            );
    };

    function request() { return { type: dataConstant.DATA_REQUEST } }
    function success(result) { return { type: dataConstant.DATA_SUCCESS, result } }
    function failure(error) { return { type: dataConstant.DATA_FAILURE, error } }
}

