import React, {useEffect, useState} from 'react'
import {Box, Button, Grid, makeStyles, Paper, Typography} from '@material-ui/core';
import DatePicker from 'react-date-picker';
import {useDispatch, useSelector} from 'react-redux';
import {dashboardAction, dataAction} from 'config/redux/actions';
import {convertDate} from 'utils/date';

const useStyles = makeStyles((theme) => ({
    root: {
        padding: theme.spacing(1, 0, 0, 1)
    },
    content: {
        padding: theme.spacing(1)
    },
    title: {
        fontWeight: theme.typography.fontWeightBold
    },
    subtitle: {},
    button: {
        margin: theme.spacing(2)
    },
    calendar: {
        padding: theme.spacing(1)
    }
}))

function UpdateLastDate() {
    const [value, onChange] = useState(new Date());
    const dispatch = useDispatch();
    const classes = useStyles();
    const date = useSelector(state => state.dashboard?.date);

    useEffect(() => {
        dispatch(dashboardAction.getLastUpdate());
    }, [])

    const update = () => {
        dispatch(dataAction.setLastUpdate(convertDate(value)));
    }
    return (
        <Paper elevation={1}>
            <Box className={classes.root}>
                <Typography className={classes.title}>Update Tanggal</Typography>
                <Typography className={classes.subtitle} color="textSecondary">Update Terakhir: {date}</Typography>
            </Box>
            <Grid container spacing={1} className={classes.content}>
                <Grid item xs={12}>
                    <DatePicker
                        onChange={onChange}
                        value={value}
                        format="dd/MM/y"
                    />
                    <br/>
                    <Button
                        style={{marginTop: 10}}
                        className="btn-upload"
                        color="primary"
                        variant="contained"
                        component="span"
                        onClick={update}>
                        Update
                    </Button>
                </Grid>
            </Grid>
        </Paper>
    )
}

export default UpdateLastDate