import axios from 'axios';
import Cookie from "js-cookie";
const qs = require('qs');

export const authService = {
    logout,
    login
};

function logout() {
    Cookie.remove('token')
    Cookie.remove('refreshToken')
}

function login(username, password) {
    return axios.request({
        url: "/oauth/token",
        method: "POST",
        baseURL: `${process.env.REACT_APP_AUTH_URL}`,
        auth: {
            username: `dashboard`,
            password: `frontend-app-password`
        },
        data: qs.stringify({
            "grant_type": "password",
            "client_id": `dashboard`,
            "username": `${username}`,
            "password": `${password}`
        })
    }).then(user => {
        Cookie.set('refreshToken', user.data.refresh_token);
        Cookie.set('token', user.data.access_token);
        return user;
    });
}