import {Box, Grid, makeStyles, Paper, Typography} from "@material-ui/core";
import MyChart from "../../../moleculas/MyChart";

const useStyles = makeStyles((theme) => ({
    root: {
        margin: theme.spacing(1),
        color: "#707070"
    },
    title: {
        padding: theme.spacing(1),
        fontWeight: "bold"
    },
}))

function GroupChart(props) {
    const classes = useStyles();
    const {
        rootStyle = classes.root,
        style,
        elevation = 2,
        title = "positive by department",
        titleStyle = classes.title,
        options = null,
    } = props;

    return (
        <Grid item xs={12} className={rootStyle}>
            <Paper elevation={elevation} className={classes.paper}>
                <Typography variant={"body2"} className={titleStyle} gutterBottom>
                    {title.toUpperCase()}
                </Typography>
                <Box>
                    <MyChart
                        style={style}
                        options={options}
                    />
                </Box>
            </Paper>
        </Grid>
    )
}

export default GroupChart;