import {dataConstant} from "../constants";

const initState = {
    loading: false
}

export function data(state = initState, action) {
    switch (action.type) {
        case dataConstant.DATA_REQUEST:
            return {
                ...state,
                loading: true
            }
        case dataConstant.DATA_FAILURE:
            return {
                ...state,
                loading: false,
                error: true,
                message: action.error
            }
        case dataConstant.DATA_SUCCESS:
            return {
                ...state,
                loading: false,
                error: false,
                results: action.result
            }
        case dataConstant.GET_INPATIENT_DATA:
            return {
                ...state,
                loading: false,
                error: false,
                inpatient: action.result
            }
        case dataConstant.GET_MESS_DATA:
            return {
                ...state,
                loading: false,
                error: false,
                mess: action.result
            }
        case dataConstant.GET_DEPARTMENTS_DATA:
            return {
                ...state,
                loading: false,
                error: false,
                places: action.result
            }
        default:
            return state;
    }
}