import React from 'react'
import { CardMedia, Grid } from '@material-ui/core'

function Media(props) {
    return (
        <Grid item xs={12}>
            <CardMedia
                className="Media"
                image={props.item.image}
                title={props.item.name}
                style={{height: 0, paddingTop: '56.25%' }}
            >
            </CardMedia>
        </Grid>
    )
}

export default Media
