import React from 'react'
import PropTypes from "prop-types";
import { makeStyles, Card, Typography, CardContent, Box, Grid, Button } from '@material-ui/core';
import TrendingDown from '@material-ui/icons/TrendingDown';
import { formatNumber } from 'utils/number';
import { TrendingUp } from '@material-ui/icons';

const useStyles = makeStyles((theme) => ({
    root: {
        minWidth: 275,
    },
    grow: {
        textAlign: "center",
        fontWeight: theme.typography.fontWeightBold,
        fontSize: "0.8rem"
    },
    growFont: {
        fontWeight: theme.typography.fontWeightBold,
        alignItems: "stretch"
    },
    title: {
        textAlign: "center",
        color: "#767676",
        fontWeight: theme.typography.fontWeightBold,
        fontSize: "0.8rem",
    },
    count: {
        textAlign: "center",
        fontWeight: theme.typography.fontWeightBold
    },
    button: {
        textAlign: "center",
        fontWeight: theme.typography.fontWeightBold
    }
}))

function CardDashboard(props) {
    const { 
        active, 
        type, 
        heading,
        heading2,
        headingTotal,
        right,
        left,
        leftGrow,
        rightGrow,
        color,
        ...otherProps 
    } = props;
    const classes = useStyles();
    const handleClick = (e) => {

    }
    return (
        <Card className={classes.root}>
            <CardContent>
                <Typography className={classes.title} color="textSecondary" gutterBottom>
                    {heading}
                </Typography>
                { (heading == "PROBABLE" || heading == "SUSPEK" || heading == "KONTAK ERAT") && (
                    <>
                    <Typography variant="h5" className={classes.count} style={{color: `${color}`}} gutterBottom>
                        {formatNumber(headingTotal)}
                    </Typography>
                    <Typography className={classes.title} color="textSecondary" gutterBottom>
                        {heading2}
                    </Typography>
                    </>
                )}
                { heading == "PROBABLE" && (
                    <Grid display="flex" container>
                        <Grid item xs={4} style={{textAlign: 'center'}}>
                            <Typography variant="h5" className={classes.count} style={{color: `#d7c368`}} gutterBottom>
                                {formatNumber(left)}
                            </Typography>
                            <Typography className={classes.title} style={{color: `#d7c368`}} gutterBottom>
                                {leftGrow}
                            </Typography>
                            <Typography className={classes.title} color="textSecondary" style={{minHeight:40}} gutterBottom>
                                Isolasi/Dalam Perawatan
                            </Typography>
                        </Grid>
                        <Grid item xs={4} style={{textAlign: 'center'}}>
                            <Typography variant="h5" className={classes.count} style={{color: `#27ae60`}} gutterBottom>
                                {formatNumber(props.middle)}
                            </Typography>
                            <Typography className={classes.title} style={{color: `#27ae60`}} gutterBottom>
                                {props.middleGrow}
                            </Typography>
                            <Typography className={classes.title} color="textSecondary" style={{minHeight:40}} gutterBottom>
                                Selesai Isolasi/Sembuh
                            </Typography>
                        </Grid>
                        <Grid item xs={4} style={{textAlign: 'center'}}>
                            <Typography variant="h5" className={classes.count} style={{color: `#a20e0e`}} gutterBottom>
                                {formatNumber(right)}
                            </Typography>
                            <Typography className={classes.title} style={{color: `#a20e0e`}} gutterBottom>
                                {rightGrow}
                            </Typography>
                            <Typography className={classes.title} color="textSecondary" style={{minHeight:40}} gutterBottom>
                                Meninggal
                            </Typography>
                        </Grid>
                    </Grid>
                )}
                { (heading == "SUSPEK" || heading == "KONTAK ERAT") && (
                    <Grid display="flex" container>
                        <Grid item xs={6} style={{textAlign: 'center'}}>
                            <Typography variant="h5" className={classes.count} style={{color: `#be8228`}} gutterBottom>
                                {formatNumber(left)}
                            </Typography>
                            <Typography className={classes.title} style={{color: `#be8228`}} gutterBottom>
                                {leftGrow}
                            </Typography>
                            <Typography className={classes.title} color="textSecondary" style={{minHeight:40}} gutterBottom>
                                {props.leftText}
                            </Typography>
                        </Grid>
                        <Grid item xs={6} style={{textAlign: 'center'}}>
                            <Typography variant="h5" className={classes.count} style={{color: `#828282`}} gutterBottom>
                                {formatNumber(right)}
                            </Typography>
                            <Typography className={classes.title} style={{color: `#828282`}} gutterBottom>
                                {rightGrow}
                            </Typography>
                            <Typography className={classes.title} color="textSecondary" style={{minHeight:40}} gutterBottom>
                                {props.rightText}
                            </Typography>
                        </Grid>
                    </Grid>
                )}
                { (heading != "PROBABLE" && heading != "KONTAK ERAT" && heading != "SUSPEK")  && (
                    <Grid display="flex" container>
                        <Grid item xs={6} style={{textAlign: 'center'}}>
                            <Typography className={classes.title} color="textSecondary" gutterBottom>
                                Kideco
                            </Typography>
                            <Typography variant="h5" className={classes.count} style={{color: `${color}`}} gutterBottom>
                                {formatNumber(left)}
                            </Typography>
                            <Button
                                variant="text"
                                color="white"
                                size="small"
                                className={classes.button}
                                style={{color: `${color}`}}
                                startIcon={leftGrow < 0 ? (
                                    <TrendingDown />
                                ) : (
                                    <TrendingUp />
                                )}
                            >
                                {formatNumber(leftGrow)}
                            </Button>
                        </Grid>
                        <Grid item xs={6} style={{textAlign: 'center'}}>
                            <Typography className={classes.title} color="textSecondary" gutterBottom>
                                Paser
                            </Typography>
                            <Typography variant="h5" className={classes.count} style={{color: `${color}`}} gutterBottom>
                                {formatNumber(right)}
                            </Typography>
                            <Button
                                variant="text"
                                color="white"
                                size="small"
                                className={classes.button}
                                style={{color: `${color}`}}
                                startIcon={rightGrow < 0 ? (
                                    <TrendingDown />
                                ) : (
                                    <TrendingUp />
                                )}
                            >
                                {formatNumber(rightGrow)}
                            </Button>
                        </Grid>
                    </Grid>
                )}
            </CardContent>
        </Card>
    )
}

CardDashboard.propTypes = {
    active: PropTypes.bool,
    type: PropTypes.string,
    heading: PropTypes.string,
};

export default CardDashboard