import React from "react";
import {Grid, makeStyles, Paper, Typography} from "@material-ui/core";
import {randomNumber} from "../../../utils/number";
import MyChart from "../MyChart";

const useStyles = makeStyles(theme => ({
    root: {
        padding: theme.spacing(0),
        color: "#707070"
    },
    title: {
        fontWeight: "bold"
    }
}))

function GroupPieChart(props) {
    const classes = useStyles();
    const {elevation = 0, title = ""} = props;
    return (
        <Grid item xs={12} className={classes.root}>
            <Paper elevation={elevation} className={classes.paper}>
                <Typography variant={"body2"} className={classes.title} gutterBottom>
                    {title}
                </Typography>
                <Grid container>
                    <Grid item xs={12} md={4}>
                        <MyChart
                            options={{
                                color: ['#D1D1D1', '#FF5A5A'],
                                title: {
                                    text: 'Boarding KM 40',
                                    left: 'center'
                                },
                                tooltip: {
                                    trigger: 'item'
                                },
                                legend: {
                                    bottom: '0%',
                                    data: ["Used", "Ready"]
                                },
                                series: [
                                    {
                                        name: 'Data',
                                        type: 'pie',
                                        radius: ['40%', '70%'],
                                        avoidLabelOverlap: false,
                                        itemStyle: {
                                            borderRadius: 10,
                                            borderColor: '#fff',
                                            borderWidth: 2
                                        },
                                        label: {
                                            show: false,
                                            position: 'center'
                                        },
                                        emphasis: {
                                            label: {
                                                show: true,
                                                fontSize: '40',
                                                fontWeight: 'bold'
                                            }
                                        },
                                        labelLine: {
                                            show: false
                                        },
                                        data: [
                                            {value: randomNumber(1, 50), name: 'Ready'},
                                            {value: randomNumber(1, 50), name: 'Used'},
                                        ]
                                    }
                                ]
                            }}
                        />
                    </Grid>
                    <Grid item xs={12} md={4}>
                        <MyChart
                            options={{
                                color: ['#D1D1D1', '#FF5A5A'],
                                title: {
                                    text: 'M/T KM40',
                                    left: 'center'
                                },
                                tooltip: {
                                    trigger: 'item'
                                },
                                legend: {
                                    bottom: '0%',
                                    data: ["Used", "Ready"]
                                },
                                series: [
                                    {
                                        name: 'Data',
                                        type: 'pie',
                                        radius: ['40%', '70%'],
                                        avoidLabelOverlap: false,
                                        itemStyle: {
                                            borderRadius: 10,
                                            borderColor: '#fff',
                                            borderWidth: 2
                                        },
                                        label: {
                                            show: false,
                                            position: 'center'
                                        },
                                        emphasis: {
                                            label: {
                                                show: true,
                                                fontSize: '40',
                                                fontWeight: 'bold'
                                            }
                                        },
                                        labelLine: {
                                            show: false
                                        },
                                        data: [
                                            {value: randomNumber(1, 50), name: 'Ready'},
                                            {value: randomNumber(1, 50), name: 'Used'},
                                        ]
                                    }
                                ]
                            }}
                        />
                    </Grid>
                    <Grid item xs={12} md={4}>
                        <MyChart
                            options={{
                                color: ['#D1D1D1', '#FF5A5A'],
                                title: {
                                    text: 'MESS 9 BC',
                                    left: 'center'
                                },
                                tooltip: {
                                    trigger: 'item'
                                },
                                legend: {
                                    bottom: '0%',
                                    data: ["Used", "Ready"]
                                },
                                series: [
                                    {
                                        name: 'Data',
                                        type: 'pie',
                                        radius: ['40%', '70%'],
                                        avoidLabelOverlap: false,
                                        itemStyle: {
                                            borderRadius: 10,
                                            borderColor: '#fff',
                                            borderWidth: 2
                                        },
                                        label: {
                                            show: false,
                                            position: 'center'
                                        },
                                        emphasis: {
                                            label: {
                                                show: true,
                                                fontSize: '40',
                                                fontWeight: 'bold'
                                            }
                                        },
                                        labelLine: {
                                            show: false
                                        },
                                        data: [
                                            {value: randomNumber(1, 50), name: 'Ready'},
                                            {value: randomNumber(1, 50), name: 'Used'},
                                        ]
                                    }
                                ]
                            }}
                        />
                    </Grid>
                </Grid>
            </Paper>
        </Grid>
    )
}

export default GroupPieChart;