import React, {useState} from 'react';
import {Paper, Grid, TextField, Button, makeStyles} from '@material-ui/core';
import {Face, Fingerprint} from '@material-ui/icons'
import {useDispatch} from "react-redux";
import {authAction} from "../../../config/redux/actions";

const useStyles = makeStyles((theme) => ({
    margin: {
        margin: theme.spacing(2),
    },
    padding: {
        padding: theme.spacing(1)
    }
}));

function SignInSection() {
    const classes = useStyles();
    const dispatch = useDispatch();
    const [username, setUsername] = useState();
    const [password, setPassword] = useState();

    const login = () => {
        if(username === "" || password === "") {
            alert("Please fill username and password")
        } else {
            dispatch(authAction.login(username, password));
        }
    }
    return (
        <Paper className={classes.padding} elevation={5}>
            <div className={classes.margin}>
                <Grid container spacing={8} alignItems="flex-end">
                    <Grid item>
                        <Face/>
                    </Grid>
                    <Grid item md={true} sm={true} xs={true}>
                        <TextField id="username" label="Username" type="email" fullWidth onChange={(e) => setUsername(e.target.value)} autoFocus required/>
                    </Grid>
                </Grid>
                <Grid container spacing={8} alignItems="flex-end">
                    <Grid item>
                        <Fingerprint/>
                    </Grid>
                    <Grid item md={true} sm={true} xs={true}>
                        <TextField id="username" label="Password" type="password" onChange={(e) => setPassword(e.target.value)} fullWidth required/>
                    </Grid>
                </Grid>
                <Grid container justify="center" style={{marginTop: '40px'}}>
                    <Button variant="outlined" color="primary" style={{textTransform: "none"}} onClick={login}>Login</Button>
                </Grid>
            </div>
        </Paper>
    );
}

export default SignInSection;