import React from "react";
import ReactECharts from 'echarts-for-react';

function MyChart(props) {
    const {options, style = { height: 400 }, opts = {renderer: 'svg'}} = props;
    return (
        <ReactECharts
            option={options}
            style={style}
            opts={opts}
        />
    )
}

export default MyChart;