import { dashboardConstant } from "../constants";
import { dashboardService } from "config/services";

export const dashboardAction = {
    get,
    getStatistic,
    getImages,
    getLastUpdate
}

function get(criteria) {
    return dispatch => {
        dispatch(request());

        dashboardService.get(criteria)
            .then(
                result => dispatch(success(result)),
                error => dispatch(failure(error.toString()))
            );
    };

    function request() { return { type: dashboardConstant.DASHBOARD_REQUEST } }
    function success(result) { return { type: dashboardConstant.DASHBOARD_SUCCESS, result } }
    function failure(error) { return { type: dashboardConstant.DASHBOARD_FAILURE, error } }
}

