import React from 'react';
import {Button, Grid, makeStyles, Paper} from "@material-ui/core";
import {CalendarToday, Toc} from "@material-ui/icons";
import {useHistory} from "react-router";

const useStyles = makeStyles((theme) => ({
    root: {
        padding: theme.spacing(1, 0, 0, 1)
    },
    content: {
        padding: theme.spacing(1)
    },
    title: {
        fontWeight: theme.typography.fontWeightBold
    },
    subtitle: {},
    button: {
        margin: theme.spacing(1),

    },
}))

function DashboardNav() {
    const classes = useStyles();
    const history = useHistory();
    return (
        <Paper elevation={0}>
            <Grid container spacing={1} className={classes.content}>
                <Grid item xs={12}>
                    <Button
                        variant="contained"
                        color="secondary"
                        className={classes.button}
                        onClick={() => history.push("/cms/statistic")}
                        startIcon={<Toc/>}
                    >
                        Statistic
                    </Button>
                    <Button
                        variant="contained"
                        color="primary"
                        className={classes.button}
                        onClick={() => history.push("/cms/inpatient")}
                        startIcon={<Toc/>}
                    >
                        In-patient Care
                    </Button>
                    <Button
                        variant="contained"
                        className={classes.button}
                        startIcon={<Toc/>}
                        onClick={() => history.push("/cms/mess")}
                    >
                        Mess Quarantine
                    </Button>
                    <Button
                        variant="contained"
                        style={{backgroundColor: "yellow"}}
                        className={classes.button}
                        startIcon={<Toc/>}
                        onClick={() => history.push("/cms/departments")}
                    >
                        Data Departments
                    </Button>
                    <Button
                        variant="contained"
                        style={{backgroundColor: "#5aaa4e"}}
                        className={classes.button}
                        startIcon={<CalendarToday/>}
                        onClick={() => history.push("/cms/date")}
                    >
                        Date
                    </Button>
                </Grid>
            </Grid>
        </Paper>
    )

}

export default DashboardNav