import * as React from 'react';
import { AppBar, Toolbar, Container, IconButton, Hidden, List, ListItem, ListItemText, makeStyles } from '@material-ui/core';
import Cookie from "js-cookie"

import SideDrawer from 'components/moleculas/SideDrawer';
import { ExitToApp } from '@material-ui/icons';

import Logo from 'assets/logo/kideco-logo.png';
import { Link, useHistory } from 'react-router-dom';
import { useDispatch } from 'react-redux';
import { useState } from 'react';
import Image from 'components/atoms/Image';

const useStyles = makeStyles((theme) => ({
    header: {
        backgroundColor: 'white',
        boxShadow: '0 5px 0 #ea0029'
    },
    navbarDisplayFlex: {
        display: `flex`,
        justifyContent: `space-between`
    },
    navListDisplayFlex: {
        display: `flex`,
        justifyContent: `space-between`
    },
    linkText: {
        textDecoration: `none`,
        textTransform: `uppercase`
    },
    stylelogout: {
        color: `#007bff`,
        textTransform: `uppercase`,
        fontSize: `0.875rem`
    }
}));

const Header = () => {
    const classes = useStyles()
    const history = useHistory();

    return (
        <AppBar position="relative" elevation={1} className={classes.header}>
            <Toolbar component="nav" className={classes.navbarDisplayFlex}>
                <Container className={classes.navbarDisplayFlex}>
                    <IconButton edge="start" aria-label="home">
                        <a onClick={() => history.push('/')} style={{ color: `white` }}>
                            <Image source={Logo} />
                        </a>
                    </IconButton>

                    <Hidden smDown>
                        <List
                            component="nav"
                            aria-labelledby="main navigation"
                            className={classes.navListDisplayFlex}
                        >
                            {!Cookie.get('token') && (
                            <Link to="/" className={classes.linkText}>
                                <ListItem button>
                                    <ListItemText secondary={`Home`} />
                                </ListItem>
                            </Link>
                            )}
                            {Cookie.get('token') && (
                                <Link to="/login" className={classes.linkText}>
                                    <ListItem button>
                                        <ListItemText secondary={`Logout`} />
                                    </ListItem>
                                </Link>
                            )}
                        </List>
                    </Hidden>
                    <Hidden mdUp>
                        <SideDrawer/>
                    </Hidden>
                </Container>
            </Toolbar>
        </AppBar>
    )
}

export default Header;