import React from 'react'
import {Route, Switch} from "react-router";
import DashboardNav from "../../../components/organisms/DashboardNav";
import {Grid, makeStyles} from "@material-ui/core";
import Statistic from "./Statistic";
import Department from "./Department";
import InPatientCare from "./InPatientCare";
import Mess from "./Mess";
import Dashboard from "./Dashboard";
import UpdateDate from "./UpdateDate";

const useStyles = makeStyles((theme) => ({
    root: {
        padding: theme.spacing(2, 0, 0, 0),
        width: "100%"
    }
}))

function Cms() {
    const classes = useStyles();
    return (
        <Grid item className={classes.root}>
            <DashboardNav/>
            <Switch>
                <Route exact path="/cms" component={Dashboard}/>
                <Route exact path="/cms/statistic" component={Statistic}/>
                <Route exact path="/cms/mess" component={Mess}/>
                <Route exact path="/cms/inpatient" component={InPatientCare}/>
                <Route exact path="/cms/departments" component={Department}/>
                <Route exact path="/cms/date" component={UpdateDate}/>
                <Route exact path="/cms/:page" component={Dashboard}/>
            </Switch>
        </Grid>

    )
}

export default Cms
