import React, {useState} from 'react'
import {
    Grid,
    Typography,
    makeStyles,
    Paper,
    Box,
    Table,
    TableHead,
    TableRow, TableBody, TableContainer, TextField, Button
} from '@material-ui/core'
import {useDispatch, useSelector} from 'react-redux';
import {useEffect} from 'react';
import {dashboardAction, dataAction} from 'config/redux/actions';
import StyledTableCell from "../../moleculas/StyledTableCell";
import DatePicker from "react-date-picker";
import {convertDate} from "../../../utils/date";

const useStyles = makeStyles((theme) => ({
    root: {
        padding: theme.spacing(1, 0, 0, 1)
    },
    content: {
        padding: theme.spacing(1)
    },
    title: {
        fontWeight: theme.typography.fontWeightBold
    },
    subtitle: {},
    button: {
        margin: theme.spacing(2)
    },
    calendar: {
        padding: theme.spacing(1)
    },
    table: {
        minWidth: 650,
    },
}))

function MessUpdate() {
    const mess = useSelector(state => state.data?.mess);
    const date = useSelector(state => state.dashboard?.date);
    const [value, onChange] = useState(new Date());
    const [places, setPlaces] = useState([])
    const dispatch = useDispatch();
    const classes = useStyles();

    useEffect(() => {
        dispatch(dataAction.getMessHistory());
        dispatch(dashboardAction.getLastUpdate());
    }, [])

    useEffect(() => {
        if (mess) {
            setPlaces(mess);
        }
    }, [mess])

    const update = () => {
        dispatch(dataAction.updateMessHistory({
            date: convertDate(value),
            histories: places
        }));
    }

    const handleChangeData = (name, value, code) => {
        setPlaces(places.map(place => {
            if (place.code === code) {
                if (name === "used") {
                    return {
                        ...place,
                        [name]: value,
                        ready: place.capacity - value
                    }
                }
                return {
                    ...place,
                    [name]: value
                }
            }

            return place;
        }))
    }

    return (
        <Paper elevation={1}>
            <Box className={classes.root}>
                <Typography className={classes.title}>Update Data Mess</Typography>
                <Typography className={classes.subtitle} color="textSecondary">Update Terakhir: {date}</Typography>
            </Box>
            <Grid container spacing={1} className={classes.content}>
                <Grid item xs={12}>
                    <TableContainer>
                        <Table className={classes.table} aria-label="customized table">
                            <TableHead>
                                <TableRow>
                                    <StyledTableCell align="center" style={{ width: 200 }}>Mess Quarantine</StyledTableCell>
                                    <StyledTableCell align="center" style={{ width: 120 }}>Capacity</StyledTableCell>
                                    <StyledTableCell align="center" style={{ width: 150 }}>Used</StyledTableCell>
                                    <StyledTableCell align="center" style={{ width: 120 }}>Ready</StyledTableCell>
                                    <StyledTableCell align="center">Remark</StyledTableCell>
                                </TableRow>
                            </TableHead>
                            <TableBody>
                                {places.map((place, key) => (
                                    <TableRow key={key}>
                                        <StyledTableCell component="th" scope="row" align="left">
                                            {place.name}
                                        </StyledTableCell>
                                        <StyledTableCell component="th" align="center" scope="row">
                                            {place.capacity}
                                        </StyledTableCell>
                                        <StyledTableCell component="th" align="center" scope="row">
                                            <TextField
                                                type="number"
                                                name="used"
                                                onChange={(e) => {
                                                    handleChangeData(e.target.name, e.target.value, place.code)
                                                }}
                                                value={place.used}
                                                InputLabelProps={{
                                                    shrink: true,
                                                }}
                                            />
                                        </StyledTableCell>
                                        <StyledTableCell component="th" align="center" scope="row">
                                            {place.ready}
                                        </StyledTableCell>
                                        <StyledTableCell component="th" align="center" scope="row">
                                            <TextField
                                                type="text"
                                                name="remark"
                                                onChange={(e) => {
                                                    handleChangeData(e.target.name, e.target.value, place.code)
                                                }}
                                                value={place.remark}
                                                InputLabelProps={{
                                                    shrink: true,
                                                }}
                                            />
                                        </StyledTableCell>
                                    </TableRow>
                                ))}
                            </TableBody>
                        </Table>
                    </TableContainer>
                </Grid>
            </Grid>
            <Grid container spacing={1} className={classes.content}>
                <Grid item xs={12}>
                    <Typography className={classes.subtitle} color="textSecondary">Date</Typography>
                    <DatePicker
                        onChange={onChange}
                        value={value}
                        className={classes.calendar}
                        format="dd/MM/y"
                    />
                </Grid>
            </Grid>


            <Button
                variant="contained"
                color="secondary"
                className={classes.button}
                onClick={() => update()}
            >
                Update
            </Button>
        </Paper>
    )
}

export default MessUpdate;
