import axios from 'axios';
import { authHeader, authFileHeader } from 'config/redux/helpers';

export const dashboardService = {
    get,
    getStatistic,
    getLastUpdate,
    getImages,
    postData,
    postFile
};

function get(criteria) {
    return axios.get(`${process.env.REACT_APP_API_URL}/endpoint`, {
        params: criteria
    }, { headers: authHeader() })
    .then(result => {
        return result.data.data
    });
}

function getStatistic() {
    return axios.get(`${process.env.REACT_APP_SERVICE_URL}/api/v2/statistic`,  { headers: authHeader() })
    .then(result => {
        return result.data
    });
}

function getLastUpdate() {
    return axios.get(`${process.env.REACT_APP_SERVICE_URL}/api/v2/setting/L01`,  { headers: authHeader() })
    .then(result => {
        return result.data
    });
}

function getImages() {
    return axios.get(`${process.env.REACT_APP_SERVICE_URL}/api/v2/settings/IMG`,  { headers: authHeader() })
    .then(result => {
        return result.data
    });
}

function postFile(file) {
    let formData = new FormData();
    formData.append("file", file);

    return axios.post(`${process.env.REACT_APP_API_URL}/endpoint`, formData, 
        { 
            headers: authFileHeader()
        }
    );
}

function postData(data) {
    return axios.post(`${process.env.REACT_APP_API_URL}/endpoint`, data)
            .then(result => {
                return result.data
            });
}