import React from 'react'
import PropTypes from "prop-types";
import {makeStyles, Card, Typography, CardContent, Grid} from '@material-ui/core';
import {formatNumber} from "../../../utils/number";

const useStyles = makeStyles((theme) => ({
    root: {
        minWidth: 275,
        backgroundColor: "#5aaa4e",
        color: "white"
    },
    grow: {
        textAlign: "center",
        fontWeight: theme.typography.fontWeightBold,
        fontSize: "0.8rem"
    },
    growFont: {
        fontWeight: theme.typography.fontWeightBold,
        alignItems: "stretch"
    },
    title: {
        color: "white",
        fontWeight: theme.typography.fontWeightBold,
        fontSize: "0.8rem",
    },
    subtitle: {
        color: "white",
        fontSize: "0.8rem",
    },
    count: {
        textAlign: "center",
        fontWeight: theme.typography.fontWeightBold
    },
    button: {
        textAlign: "center",
        fontWeight: theme.typography.fontWeightBold
    }
}))

function CardDashboardRdt(props) {
    const {
        right,
        left,
        middle,
    } = props;
    const classes = useStyles();
    return (
        <Card className={classes.root}>
            <CardContent>
                <Typography className={classes.title} color="textSecondary" gutterBottom>
                    RDT (Rapid Diagnostic Test)
                </Typography>
                <Typography className={classes.subtitle} color="textSecondary" gutterBottom>
                    Rapid Diagnostic Test (RDT) atau test diagnostik cepat merupakan test yang digunakan sebagai skrining medis awal untuk mendeteksi COVID-19. Pada hasil RDT yang Reaktif akan dilakukan pemeriksaan konfirmasi lebih lanjut dengan metode Polymerase Chain Reaction (PCR).
                </Typography>
                <Grid display="flex" container>
                    <Grid item xs={4} style={{textAlign: 'center'}}>
                        <Typography variant="h5" className={classes.count} style={{color: `#d7c368`}} gutterBottom>
                            {formatNumber(left)}
                        </Typography>
                        <Typography className={classes.title} color="textSecondary" style={{minHeight:40}} gutterBottom>
                            Jumlah RDT
                        </Typography>
                    </Grid>
                    <Grid item xs={4} style={{textAlign: 'center'}}>
                        <Typography variant="h5" className={classes.count} style={{color: `#d7c368`}} gutterBottom>
                            {formatNumber(middle)}
                        </Typography>
                        <Typography className={classes.title} color="textSecondary" style={{minHeight:40}} gutterBottom>
                            Reaktif
                        </Typography>
                    </Grid>
                    <Grid item xs={4} style={{textAlign: 'center'}}>
                        <Typography variant="h5" className={classes.count} style={{color: `#a20e0e`}} gutterBottom>
                            {formatNumber(right)}
                        </Typography>
                        <Typography className={classes.title} color="textSecondary" style={{minHeight:40}} gutterBottom>
                            Non Reaktif
                        </Typography>
                    </Grid>
                    {/* <Typography className={classes.subtitle} color="textSecondary" gutterBottom>
                        Update Terakhir: Senin, 28 Des 2020
                    </Typography> */}
                </Grid>
            </CardContent>
        </Card>
    )
}

CardDashboardRdt.propTypes = {
    active: PropTypes.bool,
    type: PropTypes.string,
    heading: PropTypes.string,
};

export default CardDashboardRdt