import React from "react";
import {Grid, makeStyles, Paper, Typography} from "@material-ui/core";
import {formatNumber} from "../../../../utils/number";

const useStyles = makeStyles((theme) => ({
    root: {
        padding: theme.spacing(1, 0, 0, 1)
    },
    title: {
        fontWeight: theme.typography.fontWeightBold
    },
    header: {
        padding: theme.spacing(2, 0, 0, 1),
        borderRadius: theme.spacing(2, 2, 0, 0),
    },
    count: {
        padding: theme.spacing(0, 0, 2, 0),
        textAlign: "center",
        fontWeight: theme.typography.fontWeightBold
    },
    paper: {
        borderRadius: theme.spacing(2)
    },
    body: {
        padding: theme.spacing(0, 0, 1, 2),
        color: "#707070"
    }
}))

function Statistic() {
    const classes = useStyles();
    return (
        <Grid container xs={12} spacing={1}>
            <Grid item xs={12} sm={12} md={6} lg={3}>
                <Paper className={classes.paper} elevation={1}>
                    <Header
                        title={`positive`.toUpperCase()}
                        count={211}
                        background={`#FF5A5A`}
                        color={`white`}
                    />
                    <Body
                        kidecoEmployee={20}
                        kidecoFamily={10}
                        mitra={30}
                        mitraFamily={10}
                    />
                </Paper>
            </Grid>
            <Grid item xs={12} sm={12} md={6} lg={3}>
                <Paper className={classes.paper} elevation={1}>
                    <Header
                        title={`in-patient care`.toUpperCase()}
                        count={62}
                        background={`#FFC34B`}
                        color={`white`}
                    />
                    <Body
                        kidecoEmployee={20}
                        kidecoFamily={10}
                        mitra={30}
                        mitraFamily={10}
                    />
                </Paper>
            </Grid>
            <Grid item xs={12} sm={12} md={6} lg={3}>
                <Paper className={classes.paper} elevation={1}>
                    <Header
                        title={`recovery`.toUpperCase()}
                        count={149}
                        background={`#4B93FF`}
                        color={`white`}
                    />
                    <Body
                        kidecoEmployee={20}
                        kidecoFamily={10}
                        mitra={30}
                        mitraFamily={10}
                    />
                </Paper>
            </Grid>
            <Grid item xs={12} sm={12} md={6} lg={3}>
                <Paper className={classes.paper} elevation={1}>
                    <Header
                        title={`decease`.toUpperCase()}
                        count={0}
                        background={`#484848`}
                        color={`white`}
                    />
                    <Body
                        kidecoEmployee={20}
                        kidecoFamily={10}
                        mitra={30}
                        mitraFamily={10}
                    />
                </Paper>
            </Grid>
        </Grid>
    )
}

function Header(props) {
    const {title, color, background, count} = props;
    const classes = useStyles();
    return (
        <div className={classes.header} style={{backgroundColor: `${background}`, color: `${color}`}}>
            <Typography variant={"body2"} className={classes.title} gutterBottom>
                {title}
            </Typography>
            <Typography variant="h4" className={classes.count} gutterBottom>
                {formatNumber(count)}
            </Typography>
        </div>
    )
}

function Body(props) {
    const {kidecoEmployee, kidecoFamily, mitra, mitraFamily} = props;
    const classes = useStyles();
    return (
        <div className={classes.body}>
            <table>
                <tr>
                    <td>Kideco</td>
                    <td>:</td>
                    <td>{formatNumber(kidecoEmployee)}</td>
                </tr>
                <tr>
                    <td>Family</td>
                    <td>:</td>
                    <td>{formatNumber(kidecoFamily)}</td>
                </tr>
                <tr>
                    <td colSpan={3} height={10}/>
                </tr>
                <tr>
                    <td>Mitra</td>
                    <td>:</td>
                    <td>{formatNumber(mitra)}</td>
                </tr>
                <tr>
                    <td>Family</td>
                    <td>:</td>
                    <td>{formatNumber(mitraFamily)}</td>
                </tr>
            </table>
        </div>
    )
}

export default Statistic;