import { exampleConstant } from "../constants"

const initState = {
    loading: false
}

export function example(state = initState, action) {
    switch (action.type) {
        case exampleConstant.EXAMPLE_REQUEST:
            return {
                ...state,
                loading: true
            }
        case exampleConstant.EXAMPLE_FAILURE:
            return {
                ...state,
                loading: false,
                error: true,
                message: action.error
            }
        case exampleConstant.EXAMPLE_SUCCESS: 
            return {
                ...state,
                loading: false,
                error: false,
                results: action.result
            }
        default:
            return state;
    }
}